import React, { useState, useEffect } from "react";
import Web3 from "web3";
import contractABI from "../abis/AutonomousVehicleDID.json";

const RegisterVehiclePage = () => {
  const [vehicleModel, setVehicleModel] = useState(""); //    
  const [vehicleNumber, setVehicleNumber] = useState(""); //    
  const [successMessage, setSuccessMessage] = useState(""); //   
  const [errorMessage, setErrorMessage] = useState(""); //   
  const [did, setDid] = useState(""); // DID  

  // MetaMask    DID 
  useEffect(() => {
    const loadAccount = async () => {
      try {
        const web3 = new Web3(window.ethereum);
        const accounts = await web3.eth.getAccounts();
        if (accounts.length > 0) {
          const account = accounts[0];
          const generatedDid = `did:ether:${account}`;
          setDid(generatedDid); // DID 
        }
      } catch (error) {
        console.error("MetaMask    .", error);
      }
    };
    loadAccount();
  }, []);

  //     
  const validateVehicleNumber = (number) => {
    const vehicleNumberRegex = /^[0-9]{2,3}[-][0-9]{4}$/; // 2~3  + 1  + 4  
    return vehicleNumberRegex.test(number); //    
  };

  //   
  const registerVehicle = async () => {
    if (!validateVehicleNumber(vehicleNumber)) {
      setErrorMessage("    . (: 123456)");
      return;
    }

    try {
      const web3 = new Web3(window.ethereum);
      const accounts = await web3.eth.getAccounts();
      const account = accounts[0];
      const contractAddress = "0xa0c58f669bf1cf9762d91ee98f5b8954ba2210ef";
      const contract = new web3.eth.Contract(contractABI, contractAddress);

      //  DID    
      const vehicle = await contract.methods.vehicles(account).call(); //     
      if (vehicle.isRegistered) {
        setErrorMessage("   ."); //      
        return;
      }

      await contract.methods
        .registerVehicle(did, vehicleModel, vehicleNumber, "")
        .send({ from: account });
      setSuccessMessage("  .");
      setErrorMessage(""); //     
    } catch (error) {
      setErrorMessage("  .");
      setSuccessMessage(""); //     
      console.error(error);
    }
  };

  return (
    <div className="register-vehicle-page">
      <h2> </h2>
      <p>YOUR DID</p>
      <p>{did || "MetaMask  "}</p> {/* DID  */}
      <input
        type="text"
        placeholder=" "
        value={vehicleModel}
        onChange={(e) => setVehicleModel(e.target.value)}
      />
      <input
        type="text"
        placeholder="  (: 123456)"
        value={vehicleNumber}
        onChange={(e) => setVehicleNumber(e.target.value)}
      />
      <button onClick={registerVehicle}></button>
      {successMessage && <p style={{ color: "green" }}>{successMessage}</p>}
      {errorMessage && <p style={{ color: "red" }}>{errorMessage}</p>}
    </div>
  );
};

export default RegisterVehiclePage;
