import React from "react";
import "./Modal.css"; //  

const Modal = ({ vehicleNumber, onAccept, onReject }) => {
  return (
    <div className="modal-overlay">
      <div className="modal">
        <h2> </h2>
        <p> : {vehicleNumber}    .</p>
        <button onClick={onAccept}></button>
        <button onClick={onReject}></button>
      </div>
    </div>
  );
};

export default Modal;
