import React, { useState, useEffect } from "react";
import { BrowserRouter as Router, Route, Routes, Link } from "react-router-dom";
import Web3 from "web3";
import contractABI from "./abis/AutonomousVehicleDID.json";
import CheckVehiclePage from "./components/Check_Vehicle_Page";
import RegisterVehiclePage from "./components/Register_Vehicle_Page";
import RequestAuthenticationPage from "./components/Request_Authentication_Page";
import LogAuthenticationPage from "./components/Log_Authentication_Page";
import Modal from "./components/Receive_Modal";
import "./App.css";

const App = () => {
  const [account, setAccount] = useState("");
  const [web3, setWeb3] = useState(null);
  const [contract, setContract] = useState(null);
  const [errorMessage, setErrorMessage] = useState("");
  const [showModal, setShowModal] = useState(false);
  const [requestDetails, setRequestDetails] = useState(null);
  const [requestResult, setRequestResult] = useState("");
  const [connectedVehicle, setConnectedVehicle] =
    useState("  .");
  const [connectedVehicleNumber, setConnectedVehicleNumber] = useState("");

  // Web3  MetaMask  
  useEffect(() => {
    const loadWeb3 = async () => {
      if (window.ethereum) {
        try {
          const web3Instance = new Web3(window.ethereum);
          await window.ethereum.request({ method: "eth_requestAccounts" });
          const accounts = await web3Instance.eth.getAccounts();
          setAccount(accounts[0]);
          setWeb3(web3Instance);
          const contractInstance = new web3Instance.eth.Contract(
            contractABI,
            "0xa0c58f669bf1cf9762d91ee98f5b8954ba2210ef"
          );
          setContract(contractInstance);
          console.log("MetaMask  ");
        } catch (error) {
          setErrorMessage("MetaMask  .  .");
          console.error("MetaMask  :", error);
        }
      } else {
        setErrorMessage(
          "MetaMask  . MetaMask ."
        );
        console.error("MetaMask  .");
      }
    };

    loadWeb3();
  }, []);

  //  
  useEffect(() => {
    const fetchEvents = async () => {
      if (contract && account) {
        try {
          const events = await contract.getPastEvents(
            "AuthenticationRequested",
            {
              fromBlock: "latest",
            }
          );
          // receiver 
          events.forEach((event) => {
            const { vehicleNumber, requester, receiver } = event.returnValues;
            if (receiver.toLowerCase() === account.toLowerCase()) {
              console.log("   :", event);
              setRequestDetails({ vehicleNumber, requester });
              setShowModal(true);
            }
          });
        } catch (error) {
          console.error("  :", error);
        }
      }
    };

    const intervalId = setInterval(fetchEvents, 1000); // 1 
    return () => clearInterval(intervalId); //    
  }, [contract, account]);

  //    
  const handleAccept = async () => {
    await contract.methods
      .acceptAuthentication(requestDetails.vehicleNumber)
      .send({ from: account });

    setRequestResult("  .");
    setConnectedVehicleNumber(requestDetails.vehicleNumber); //    
    setShowModal(false);
  };

  //    
  const handleReject = async () => {
    await contract.methods
      .rejectAuthentication(requestDetails.vehicleNumber)
      .send({ from: account });

    setRequestResult("  .");
    setShowModal(false);
  };

  //   
  const handleDisconnect = () => {
    setConnectedVehicle("  .");
    setConnectedVehicleNumber(""); //    
  };

  return (
    <Router>
      <div className="app">
        <h1>    DApp</h1>
        {account ? (
          <div>
            <p>MetaMask  : {account}</p>
            <p>{connectedVehicle}</p>
            {connectedVehicleNumber && (
              <p>  : {connectedVehicleNumber}</p>
            )}
            {connectedVehicle !== "  ." && (
              <button onClick={handleDisconnect}> </button>
            )}
          </div>
        ) : (
          <p style={{ color: "red" }}>
            {errorMessage || "MetaMask   ..."}
          </p>
        )}

        <nav>
          <ul>
            <li>
              <Link to="/DID_project/register"> </Link>
            </li>
            <li>
              <Link to="/DID_project/check">  </Link>
            </li>
            <li>
              <Link to="/DID_project/request-authentication"> </Link>
            </li>
            <li>
              <Link to="/DID_project/logs">  </Link>
            </li>
          </ul>
        </nav>
        <Routes>
          <Route
            path="/DID_project/register"
            exact
            element={<RegisterVehiclePage />}
          />
          <Route
            path="/DID_project/check"
            exact
            element={<CheckVehiclePage />}
          />
          <Route
            path="/DID_project/request-authentication"
            exact
            element={<RequestAuthenticationPage />}
          />
          <Route
            path="/DID_project/logs"
            exact
            element={<LogAuthenticationPage />}
          />
          <Route path="/DID_project" exact element={<HomePage />} />{" "}
          {/*    */}
        </Routes>
        {showModal && (
          <Modal
            message={` ${requestDetails.vehicleNumber}   .`}
            onAccept={handleAccept}
            onReject={handleReject}
            requestResult={requestResult}
          />
        )}
      </div>
    </Router>
  );
};

const HomePage = () => <div>Welcome to the Autonomous Vehicle DID DApp</div>;

export default App;
